.slider-wrap {
    position: relative;
    height: 20px;
    width: 400px;
    padding: 20px 0;
    margin: 10px 30px;
}
.line {
    position: absolute;
    width: 400px;
    height: 20px;
    background-color: #bbb;
    border-radius: 10px;
}
.cover-line {
    position: absolute;
    width: 0px;
    height: 20px;
    background-color: #666;
    border-radius: 10px;
}
.point {
    background-color: #fff;
    height: 60px;
    width: 60px;
    border-radius: 50%;
    position: absolute;
    cursor: pointer;
    transform: translateX(-50%);
    transition: border-color .3s, box-shadow .6s;
    box-shadow: 0;
    border: 10px solid #bae7ff;
    margin-top: -20px;
    /* z-index: -1; */
}
.point:focus {
    box-shadow: 0 0 0 20px rgba(24, 144, 255, .12);
    border-color: #46a6ff;
    outline: none;
}
.point:after {
    position: absolute;
    inset: -10px;
    content: "";
}
*, *:before, *:after {
    box-sizing: border-box;
}